import React from "react";
import styled from "styled-components";
import { Jumbotron, Carousel } from "react-bootstrap";
import { RichText } from "prismic-reactjs";

const SliderWrapper = styled.div`
  box-shadow: ${(props) => props.theme.BOX_SHADOW};
`;

interface Props {
  data: any;
}

interface Slide {
  image: any;
  title: any;
  description: any;
}

const Slider: React.FC<Props> = ({ data }) => {
  const { slides } = data;

  const generateSlides = () => {
    return slides.map((slide: Slide, i: number) => {
      const { image, title, description } = slide;
      return (
        <Carousel.Item key={i}>
          <img className="w-100" src={image.url} alt={image.alt} />
          <Carousel.Caption>
            {title && <RichText render={title} />}
            {description && <RichText render={description} />}
          </Carousel.Caption>
        </Carousel.Item>
      );
    });
  };

  return (
    <SliderWrapper>
      <Jumbotron fluid>
        <Carousel interval={null} fade={true}>
          {generateSlides()}
        </Carousel>
      </Jumbotron>
    </SliderWrapper>
  );
};

export default Slider;
