import React from "react";
import { Alert } from "react-bootstrap";

import WaitingScreen from "./WaitingScreen";

interface Props {
  response: any;
  isLoading?: boolean;
  children: any;
}

const WaitingForData: React.FC<Props> = ({ response, isLoading, children }) => {
  if (isLoading || response?.loading) {
    return <WaitingScreen />;
  } else if (response.error) {
    return <Alert variant="danger">{response.error}</Alert>;
  } else {
    return children;
  }
};

export default WaitingForData;
