const ENGLISH_LANG = "en-gb";
const POLISH_LANG = "pl";
export const DEFAULT_LANG = POLISH_LANG;
export const LANGS = [POLISH_LANG, ENGLISH_LANG];

const LOCAL_STORAGE_KEY = "locale";
const localStorageLocale = localStorage.getItem(LOCAL_STORAGE_KEY);
export const getInitialLocale = () =>
  localStorageLocale ? localStorageLocale : DEFAULT_LANG;
export const getInactiveLanguageLabel = (lang: string) =>
  lang === POLISH_LANG ? "English" : "Polski";
export const getInactiveLanguageKey = (lang: string) =>
  lang === POLISH_LANG ? ENGLISH_LANG : POLISH_LANG;
