import React, { useState, createContext } from "react";
import { DEFAULT_LANG } from "../locale";

interface LocaleContext {
  locale: string;
  saveLocale: (value: string) => void;
}

const LOCALE_DEFAULT_VALUE: LocaleContext = {
  locale: DEFAULT_LANG,
  saveLocale: () => {},
};

const LocaleContext = createContext<LocaleContext>(LOCALE_DEFAULT_VALUE);
const defaultLocale = DEFAULT_LANG;

interface Props {
  children: any;
  locale: string;
}

export const LocaleProvider: React.FC<Props> = ({ children, locale }) => {
  const [currentLocale, setCurrentLocale] = useState(locale || defaultLocale);

  const saveLocale = (value: string) => setCurrentLocale(value);

  return (
    <LocaleContext.Provider value={{ locale: currentLocale, saveLocale }}>
      {children}
    </LocaleContext.Provider>
  );
};

export const LocaleConsumer = LocaleContext.Consumer;

export default LocaleContext;
