import React, { useState } from "react";
import { RichText } from "prismic-reactjs";
import { Container, Card, Row, Col, Modal, Button } from "react-bootstrap";
import styled from "styled-components";

import { useLocale } from "../../hooks/useLocale";
import { DEFAULT_LANG } from "../../locale";

interface Props {
  data: any;
}

const CardWrapper = styled.div`
  /* .card {
    background-color: ${(props) => props.theme.THIRD};
  } */
`;

const Portfolio: React.FC<Props> = ({ data }) => {
  const { locale } = useLocale();
  const [modalData, setModalData] = useState(null as any);
  const { title, body: buildingsData, _meta: meta } = data;

  const generateBuildingCards = () => {
    return buildingsData.map((buildingData: any, i: number) => {
      const { image, name, description } = buildingData.primary;

      return (
        <Col sm={12} md={6} lg={3} className="mb-3" key={i}>
          <CardWrapper>
            <Card>
              {image && <Card.Img src={image.url} />}
              <Card.Body>
                <RichText render={name} />
                <Button onClick={() => setModalData({ name, description })}>
                  {locale === DEFAULT_LANG ? "Więcej" : "Show Details"}
                </Button>
              </Card.Body>
            </Card>
          </CardWrapper>
        </Col>
      );
    });
  };

  return (
    <Container id={meta.uid}>
      <RichText render={title} />
      <Row>{generateBuildingCards()}</Row>
      <Modal show={modalData} onHide={() => setModalData(null)}>
        <Modal.Header closeButton>
          {modalData && <RichText render={modalData.name} />}
        </Modal.Header>
        <Modal.Body>
          {modalData && <RichText render={modalData.description} />}
        </Modal.Body>
      </Modal>
    </Container>
  );
};

export default Portfolio;
