import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import { RichText } from "prismic-reactjs";
import styled from "styled-components";

interface Props {
  data: any;
}

const ContactWrapper = styled.div`
  background-color: ${(props) => props.theme.LOGO_BLUE};
  box-shadow: ${(props) => props.theme.BOX_SHADOW};
  
  h1, h2, h3, h4, h5, h6 {
    color: ${(props) => props.theme.SECONDARY} !important;
  }
`;


const Contact: React.FC<Props> = ({ data }) => {
  const {
    title,
    contact_details: contactDetails,
    business_register_info: businessRegisterInfo,
    _meta: meta,
  } = data;
  return (
    <ContactWrapper>
      <Container id={meta.uid}>
        <RichText render={title} />
        <Row>
          <Col sm={12} md={6}>
            {contactDetails && <RichText render={contactDetails} />}
          </Col>
          <Col sm={12} md={6}>
            {businessRegisterInfo && <RichText render={businessRegisterInfo} />}
          </Col>
        </Row>
      </Container>
    </ContactWrapper>
  );
};

export default Contact;
