import { gql } from "apollo-boost";

export const HEADER_QUERY = gql`
  query HeaderQuery($locale: String) {
    allNavigations(lang: $locale) {
      edges {
        node {
          body {
            ... on NavigationBodyLink {
              primary {
                content_relationship {
                  ... on Portfolio {
                    _meta {
                      uid
                    }
                  }
                  ... on Servicesnew {
                    _meta {
                      uid
                    }
                  }
                  ... on Contact {
                    _meta {
                      uid
                    }
                  }
                  ... on Team {
                    _meta {
                      uid
                    }
                  }
                }
                link_name
              }
            }
          }
        }
      }
    }
    allHomepages(lang: $locale) {
      edges {
        node {
          logo {
            ... on Logo {
              _meta {
                uid
              }
              logo
            }
          }
          slider {
            ... on Image_slider {
              slides {
                image
                title
                description
              }
              _meta {
                uid
              }
            }
          }
          about {
            ... on About {
              title
              content
              _meta {
                uid
              }
            }
          }
          team {
            ... on Team {
              title
              _meta {
                uid
              }
              body {
                ... on TeamBodyTeam_member {
                  type
                  label
                  primary {
                    description
                    image
                    name
                  }
                }
              }
            }
          }
          services {
            ... on Servicesnew {
              title
              _meta {
                uid
              }
              body {
                ... on ServicesnewBodyService {
                  primary {
                    service_description
                    service_name
                  }
                }
              }
            }
          }
          portfolio {
            ... on Portfolio {
              title
              _meta {
                uid
              }
              body {
                ... on PortfolioBodyBuilding {
                  type
                  label
                  primary {
                    description
                    image
                    name
                  }
                }
              }
            }
          }
          contact {
            ... on Contact {
              title
              contact_details
              _meta {
                uid
              }
              business_register_info
            }
          }
        }
      }
    }
  }
`;
