import React from "react";
import { useQuery } from "@apollo/react-hooks";
import styled from "styled-components";

import { useLocale } from "./hooks/useLocale";
import WaitingForData from "./components/WaitingForData";
import Layout from "./components/Layout";
import Homepage from "./components/Homepage";
import { HEADER_QUERY } from "./queries";

const AppWrapper = styled.div`
  min-height: 100vh;
  background-color: ${(props) => props.theme.BACKGROUND};
  background-image: url("concrete2.jpg");
  background-size: contain;
  color: ${(props) => props.theme.FONT};

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    color: ${(props) => props.theme.LOGO_BLUE};
  }

  .jumbotron {
    padding: 0;
  }
  .container {
    padding-top: 20px;
  }
`;

function App() {
  const { locale } = useLocale();
  const response = useQuery(HEADER_QUERY, {
    variables: { locale },
  });

  return (
    <AppWrapper>
      <WaitingForData response={response}>
        <Layout data={response}>
          <Homepage data={response} />
        </Layout>
      </WaitingForData>
    </AppWrapper>
  );
}

export default App;
