import React from "react";

import Header from "./Header";

interface Props {
  children: any;
  data: any;
}

const Layout: React.FC<Props> = ({ children, data }) => {
  const { data: layoutData } = data;
  const { body: headerData } = layoutData.allNavigations.edges[0].node;
  return (
    <>
      <Header data={headerData} />
      {children}
    </>
  );
};

export default Layout;
