import React from "react";
import { Container, Image } from "react-bootstrap";

interface Props {
  data: any;
}

const Logo: React.FC<Props> = ({ data }) => {
  const { logo, _meta: meta } = data;
  return (
    <Container id={meta.uid}>
      <Image className="d-block mx-auto mw-100" src={logo.url} alt={logo.alt} />
    </Container>
  );
};

export default Logo;
