import React, { useState } from "react";
import { Container, Tab, Row, Col, Nav } from "react-bootstrap";
import { RichText } from "prismic-reactjs";
import styled from "styled-components";

interface IPrimary {
  service_name: string;
  service_description: any;
}

interface IService {
  primary: IPrimary;
}

interface Props {
  data: {
    body: IService[];
    title: any;
    _meta: {
      uid: string;
    };
  };
}

const ServicesWrapper = styled.div`
  .container {
    background-color: ${(props) => props.theme.SECONDARY};
    box-shadow: ${(props) => props.theme.BOX_SHADOW};
    padding-bottom: 20px;
  }
  margin-bottom: 40px;
`;

const Services: React.FC<Props> = ({ data }) => {
  const { title, body: services, _meta: meta } = data;
  const [selectedService, setSelectedService] = useState(
    services[0].primary.service_name
  );
  return (
    <ServicesWrapper>
      <Container id={meta.uid}>
        <RichText render={title} />
        <Tab.Container id="left-tabs-example" defaultActiveKey={selectedService}>
          <Row>
            <Col sm={3}>
              <Nav
                variant="pills"
                className="flex-column"
                onSelect={(key: any) => setSelectedService(key)}
              >
                {services.map((service, i) => {
                  const { service_name: serviceName } = service.primary;
                  return (
                    <Nav.Item key={serviceName}>
                      <Nav.Link
                        active={selectedService === serviceName}
                        eventKey={serviceName}
                      >
                        {serviceName}
                      </Nav.Link>
                    </Nav.Item>
                  );
                })}
              </Nav>
            </Col>
            <Col sm={9}>
              <Tab.Content>
                {services.map((service) => {
                  const {
                    service_name: serviceName,
                    service_description: serviceDescription,
                  } = service.primary;
                  return (
                    <Tab.Pane eventKey={serviceName} key={serviceName}>
                      {serviceDescription && (
                        <RichText render={serviceDescription} />
                      )}
                    </Tab.Pane>
                  );
                })}
              </Tab.Content>
            </Col>
          </Row>
        </Tab.Container>
      </Container>
    </ServicesWrapper>
  );
};

export default Services;
