import React from "react";
import styled from "styled-components";
import { Navbar, Nav } from "react-bootstrap";

import { useLocale } from "../../../hooks/useLocale";
import {
  getInactiveLanguageLabel,
  getInactiveLanguageKey,
} from "../../../locale";

const NavbarWrapper = styled.div`
  position: sticky;
  top: 0;
  z-index: 1040;
  box-shadow: ${(props) => props.theme.BOX_SHADOW};
`;

interface Props {
  data: any[];
}

const Header: React.FC<Props> = ({ data }) => {
  const { locale, saveLocale } = useLocale();

  const generateNavLinks = () => {
    return data.map((link: any) => {
      const { link_name, content_relationship } = link.primary;
      const { uid } = content_relationship._meta;
      return (
        <Nav.Link href={`#${uid}`} key={uid}>
          {link_name}
        </Nav.Link>
      );
    });
  };

  const languageSwitchHandler = () => {
    const currentLocale = getInactiveLanguageKey(locale);
    saveLocale(currentLocale);
  };

  return (
    <NavbarWrapper>
      <Navbar
        collapseOnSelect
        expand="sm"
        bg="dark"
        variant="dark"
        sticky="top"
      >
        <Navbar.Brand href="#logo">PUMM</Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="mr-auto">{generateNavLinks()}</Nav>
          <Nav>
            <Nav.Link onClick={languageSwitchHandler}>
              {getInactiveLanguageLabel(locale)}
            </Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    </NavbarWrapper>
  );
};

export default Header;
